import React from 'react'
import { withRouteData, Link } from 'react-static'
//

export default withRouteData(({posts}) => {
  return (
    <div>
      <h1>Blog</h1>
      <br />
      Tous les billets:
      <ul>
        {posts && posts.map(post => (
          post.slug &&
            <li key={post.slug}>
              <Link to={`/blog/${post.slug}/`}>{post.title}</Link>
            </li>
        ))}
      </ul>
    </div>
  )
})
